<template>
  <div id="roomInfo">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">房屋详情</div>
        <div class="van-nav-bar__right">
          <!--<img src="static/img/icon_sc_spxq.png" style="width: 20px;"/>-->
          <!--<img src="static/img/icon_fx_spxq.png" style="width: 20px;"/>-->
        </div>
      </div>
    </div>
    <div class="body_box" style="padding: 5px">
      <van-swipe class="goods-swipe" :autoplay="3000" v-if="roomInfo.sameimg">
        <van-swipe-item  v-for="(item,index) in roomInfo.sameimg.split(',')" :key="index">
          <img :src="item" width="100%" height="200px" @click="imgPreview(index)">
        </van-swipe-item>
      </van-swipe>
      <div class="background_color_ffffff" style="width: 90%;margin-left: 5%;position: relative;bottom:20px;border-radius: 5px">
        <div style="display: inline-block;height: 50px;width: 18%" class="text_center">
          <img :src="roomInfo.user.userimg" width="40px" style="margin-top: 5px;">
        </div>
        <div style="display: inline-block;height: 46px;width: 78%;vertical-align: bottom;">
          <div>
            <span class="font_14 color_8b8b8b">{{roomInfo.user.username}}</span>
            <span class="float_right font_12 color_c10804">{{roomInfo.tell}}</span>
          </div>
          <!--<br>-->
          <div class="font_12">
            <img src="static/img/vipp.png" v-if="roomInfo.user.primary=='Y'||roomInfo.user.senior=='Y'"/>
            <span class="font_12 color_fcba62" v-if="roomInfo.user.primary=='Y'||roomInfo.user.senior=='Y'">会员</span>
            <span class="float_right color_8b8b8b">发布时间:{{roomInfo.addtime}}</span>
          </div>
        </div>
      </div>
      <div class="font_14" style="background-color: #f5fafd;padding: 4px 8px;border-radius: 7px;">
        <div class="font_weight_bold" style="margin: 5px 0;">{{roomInfo.title}}</div>
        <div class="color_8b8b8b font_12" style="margin: 5px 0;">{{roomInfo.metre}}㎡</div>
        <div class="font_12 color_c10804" style="margin: 5px 0;">￥{{roomInfo.price}}万</div>
      </div>
      <div class="font_12 color_c10804" style="background-color: #f5fafd;padding: 8px;border-radius: 7px;margin-top: 5px">
        <img src="static/img/ico1111.png" style="vertical-align: middle;"/>
        {{roomInfo.address}}
      </div>
      <div class="font_12" style="background-color: #f5fafd;padding: 8px;border-radius: 7px;margin-top: 5px;line-height: 20px">
        <div>
          <label style="display: inline-block;width: 20%">楼层:</label>
          <label class="color_8b8b8b">{{roomInfo.louceng}}层</label>
        </div>
        <div>
          <label style="display: inline-block;width: 20%">装修情况:</label>
          <label class="color_8b8b8b">{{roomInfo.zhuangxiu}}</label>
        </div>
        <div>
          <label style="display: inline-block;width: 20%">朝向:</label>
          <label class="color_8b8b8b">{{roomInfo.chaoxiang}}</label>
        </div>
        <div>
          <label style="display: inline-block;width: 20%">所在区域:</label>
          <label class="color_8b8b8b">{{roomInfo.area}}</label>
        </div>
        <div>
          <label style="display: inline-block;width: 20%">户型:</label>
          <label class="color_8b8b8b">{{roomInfo.huxing.split(',')[0]}}室
                 {{roomInfo.huxing.split(',')[1]}}厅
                 {{roomInfo.huxing.split(',')[2]}}卫
                 {{roomInfo.huxing.split(',')[3]}}厨
                 {{roomInfo.huxing.split(',')[4]}}阳台
          </label>
        </div>
        <div>
          <label style="display: inline-block;width: 20%">车库:</label>
          <label class="color_8b8b8b">{{roomInfo.cheku}}㎡</label>
        </div>
        <div>
          <label style="display: inline-block;width: 20%">储藏室:</label>
          <label class="color_8b8b8b">{{roomInfo.chucang}}㎡</label>
        </div>
        <div>
          <label style="display: inline-block;width: 20%">房屋配置:</label>
          <label class="color_8b8b8b">{{roomInfo.label}}</label>
        </div>
        <div>
          <label style="display: inline-block;width: 20%">付款方式:</label>
          <label class="color_8b8b8b">{{roomInfo.paytype}}</label>
        </div>
      </div>
      <div class="font_14" style="margin-top: 5px">
        房源概括
      </div>
      <div style="margin-top: 5px" class="font_12 color_8b8b8b line_height_16">
        {{roomInfo.content}}
      </div>
    </div>
  </div>
</template>

<script>
  import { ImagePreview } from 'vant';
  export default {
    data() {
      return {
        userId:localStorage.getItem('userId'),
        samecityId:'',
        roomInfo:[],
      }
    },
    mounted() {
      this.samecityId = this.$store.state.roomInfo.samecityid;
      this.getRoomInfo();
    },
    methods: {
      imgPreview(index){
        ImagePreview(
          {
            images:this.roomInfo.sameimg.split(','),
            showIndicators:true,
            startPosition:index,
            showIndex:true,
          }
        );
      },
      getRoomInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          samecityid:$this.samecityId,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecityinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.roomInfo = rpdata.data.data;
            console.log($this.roomInfo);
          }
        })
        .catch(function(err) {})
      },
    }
  }
</script>

<style>
  #roomInfo .body_box{
    margin-top: 50px;
  }
  #roomInfo .van-search input {
    color: #8b8b8b;
  }
  #roomInfo .typeList span {
    font-size: 14px;
    color: rgb(102, 102, 102)
  }
  #roomInfo {
    width: 100%;
    background-color: white;
  }
  #roomInfo .header {
    height: 44px;
    line-height: 44px;
    box-shadow: 0px 2px 10px lightgrey;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
    background-color: white;
  }
  #roomInfo .header img {
    vertical-align: text-top;
  }
  #roomInfo .search {
    border: 1px solid lightgray;
    border-radius: 30px;
    margin-top: 6px;
  }
  #roomInfo .header .van-col {
    text-align: center;
  }
  #roomInfo .van-search {
    padding: 0px;
  }
  #roomInfo .van-search {
    background-color: transparent!important;
  }
  #roomInfo .swiper img {
    margin: 0 auto;
    display: block;
  }
  #roomInfo .nav img {
    width: 100%;
    height: auto;
  }
  #roomInfo .navCon span {
    font-size: 13px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
  }
  #roomInfo .rightText div {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 16px;
  }
  #roomInfo .footer .van-col div {
    text-align: center;
    font-size: 14px;
  }
  #roomInfo .swiperTwo .van-swipe__indicators{
    display: none;
  }
  #roomInfo .swiperTwo{
    margin-top: 44px;
  }
  #roomInfo .van-field__control {
    font-size: 12px;
  }
</style>



